"use client"

import React, { useState, useEffect } from 'react';
import { User, Calendar, Clock, Users, UserCheck, Heart, FileText, Search, ExternalLink } from 'lucide-react';



interface FormErrors {
  [key: string]: string;
}

interface Patient {
  id: string;
  name: string;
  age: number;
  phone: string;
}

interface Doctor {
  id: string;
  name: string;
  phone: string;
}

interface Volunteer {
  id: string;
  name: string;
  phone: string;
}

interface Nurse {
  id: string;
  name: string;
  phone: string;
}
interface FormData {
  assignmentId: string;
  date: string;
  time: string;
  patientId: string;
  doctorId: string;
  volunteerId: string;
  nurseId: string;
  description: string;
}
const AssignmentForm = () => {
  const [formData, setFormData] = useState<FormData>({
    assignmentId: '',
    date: '',
    time: '',
    patientId: '',
    doctorId: '',
    volunteerId: '',
    nurseId: '',
    description: ''
  });

  const [errors, setErrors] = useState<FormErrors>({});

  // Data states
  const [patients, setPatients] = useState<Patient[]>([]);
  const [doctors, setDoctors] = useState<Doctor[]>([]);
  const [volunteers, setVolunteers] = useState<Volunteer[]>([]);
  const [nurses, setNurses] = useState<Nurse[]>([]);

  // Search states
  const [patientSearch, setPatientSearch] = useState('');
  const [doctorSearch, setDoctorSearch] = useState('');
  const [volunteerSearch, setVolunteerSearch] = useState('');
  const [nurseSearch, setNurseSearch] = useState('');

  // Dropdown visibility states
  const [showPatientDropdown, setShowPatientDropdown] = useState(false);
  const [showDoctorDropdown, setShowDoctorDropdown] = useState(false);
  const [showVolunteerDropdown, setShowVolunteerDropdown] = useState(false);
  const [showNurseDropdown, setShowNurseDropdown] = useState(false);

  // Counter for PT ID generation
  const [assignmentCounter, setAssignmentCounter] = useState(1);

  // API functions
  const fetchPatients = async () => {
    try {
      const response = await fetch('https://localhost:7112/api/Hpforms/all-patients');
      const data = await response.json();
      setPatients(data);
    } catch (error) {
      console.error('Error fetching patients:', error);
      setPatients([]);
    }
  };

  const fetchDoctors = async () => {
    try {
      const response = await fetch('https://localhost:7112/api/Doctors/all-doctors');
      const data = await response.json();
      setDoctors(data);
    } catch (error) {
      console.error('Error fetching doctors:', error);
    }
  };


  const fetchVolunteers = async () => {
    try {
      const response = await fetch('https://localhost:7112/api/Volunteers/all-volunteers');
      const data = await response.json();
      setVolunteers(data);
    } catch (error) {
      console.error('Error fetching volunteers:', error);
    }
  };

  const fetchNurses = async () => {
    try {
      const response = await fetch('https://localhost:7112/api/Nurses/all-nurses');
      const data = await response.json();
      setNurses(data);
    } catch (error) {
      console.error('Error fetching nurses:', error);
    }
  };


  // Generate PT ID function
  const generateAssignmentId = () => {
    const paddedNumber = assignmentCounter.toString().padStart(3, '0');
    return `PT-${paddedNumber}`;
  };

  // Initialize form data and fetch API data
  useEffect(() => {
    const getCurrentDateTime = () => {
      const now = new Date();
      const date = now.toISOString().split('T')[0];
      const time = now.toTimeString().slice(0, 5);
      return { date, time };
    };

    const { date, time } = getCurrentDateTime();
    setFormData(prev => ({
      ...prev,
      assignmentId: generateAssignmentId(),
      date: date,
      time: time
    }));

    // Fetch data from APIs
    fetchPatients();
    fetchDoctors();
    fetchVolunteers();
    fetchNurses();
  }, []);

  // Filter functions
  const filterPatients = () => {
    if (!patientSearch.trim()) return patients;
    return patients.filter(patient =>
      patient.id.toLowerCase().includes(patientSearch.toLowerCase()) ||
      patient.name.toLowerCase().includes(patientSearch.toLowerCase()) ||
      patient.age.toString().includes(patientSearch) ||
      patient.phone.includes(patientSearch)
    );
  };

  const filterDoctors = () => {
    if (!doctorSearch.trim()) return doctors;
    return doctors.filter(doctor =>
      doctor.id.toLowerCase().includes(doctorSearch.toLowerCase()) ||
      doctor.name.toLowerCase().includes(doctorSearch.toLowerCase()) ||
      doctor.phone.includes(doctorSearch)
    );
  };

  const filterVolunteers = () => {
    if (!volunteerSearch.trim()) return volunteers;
    return volunteers.filter(volunteer =>
      volunteer.id.toLowerCase().includes(volunteerSearch.toLowerCase()) ||
      volunteer.name.toLowerCase().includes(volunteerSearch.toLowerCase()) ||
      volunteer.phone.includes(volunteerSearch)
    );
  };

  const filterNurses = () => {
    if (!nurseSearch.trim()) return nurses;
    return nurses.filter(nurse =>
      nurse.id.toLowerCase().includes(nurseSearch.toLowerCase()) ||
      nurse.name.toLowerCase().includes(nurseSearch.toLowerCase()) ||
      nurse.phone.includes(nurseSearch)
    );
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));

    if (errors[name]) {
      setErrors(prev => ({
        ...prev,
        [name]: ''
      }));
    }
  };

  const validateForm = (): boolean => {
    const newErrors: FormErrors = {};

    if (!formData.patientId) newErrors.patientId = 'Please select a patient';
    if (!formData.doctorId) newErrors.doctorId = 'Please select a doctor';
    if (!formData.volunteerId) newErrors.volunteerId = 'Please select a volunteer';
    if (!formData.nurseId) newErrors.nurseId = 'Please select a nurse';
    if (!formData.description.trim()) newErrors.description = 'Please provide a description';

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e: React.MouseEvent<HTMLButtonElement>) => {
    e.preventDefault();

    const currentTime = new Date().toTimeString().slice(0, 5);
    const submissionData = {
      ...formData,
      time: currentTime
    };

    if (validateForm()) {
      try {
        // Prepare data for backend API
        const apiData = {
          assignmentId: submissionData.assignmentId,
          date: submissionData.date,
          time: submissionData.time,
          patientId: submissionData.patientId,
          doctorId: submissionData.doctorId,
          volunteerId: submissionData.volunteerId,
          nurseId: submissionData.nurseId,
          description: submissionData.description
        };

        console.log('Sending assignment data:', apiData);

        const response = await fetch('https://localhost:7112/api/PatientAssignments', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(apiData),
        });

        if (!response.ok) {
          const errorText = await response.text();
          throw new Error(`HTTP error! status: ${response.status}, message: ${errorText}`);
        }

        const result = await response.json();
        console.log('Assignment created successfully:', result);

        // Increment counter for next assignment
        setAssignmentCounter(prev => prev + 1);

        // Generate new assignment ID for next form
        setFormData(prev => ({
          ...prev,
          assignmentId: `PT-${(assignmentCounter + 1).toString().padStart(3, '0')}`
        }));

        alert('Assignment created successfully!');

        // Optional: Reset form or redirect
        // resetForm(); // Uncomment if you want to reset the form after successful submission

      } catch (error) {
        console.error('Error creating assignment:', error);
        const errorMessage = error instanceof Error ? error.message : 'An unknown error occurred';
        alert(`Failed to create assignment: ${errorMessage}`);
      }
    }
  };

  // Optional: Add a reset form function
  const resetForm = () => {
    setFormData({
      assignmentId: generateAssignmentId(),
      date: new Date().toISOString().split('T')[0],
      time: new Date().toTimeString().slice(0, 5),
      patientId: '',
      doctorId: '',
      volunteerId: '',
      nurseId: '',
      description: ''
    });

    // Reset search states
    setPatientSearch('');
    setDoctorSearch('');
    setVolunteerSearch('');
    setNurseSearch('');

    // Reset errors
    setErrors({});
  };

  const SearchableSelect = ({
    label,
    value,
    searchValue,
    setSearchValue,
    showDropdown,
    setShowDropdown,
    items,
    onSelect,
    renderItem,
    editUrl,
    error
  }: any) => (
    <div className="relative">
      <label className="block text-sm font-semibold text-gray-700 mb-2 uppercase tracking-wide">
        {label} <span className="text-red-500">*</span>
      </label>
      <div className="relative">
        <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 w-4 h-4 text-gray-400" />
        <input
          type="text"
          value={searchValue}
          onChange={(e) => {
            setSearchValue(e.target.value);
            setShowDropdown(true);
          }}
          onFocus={() => setShowDropdown(true)}
          className={`w-full p-2 pl-10 pr-10 border-2 rounded-lg text-gray-800 font-medium transition-all duration-300 text-sm
            ${error
              ? 'border-red-400 bg-red-50 focus:border-red-500 focus:ring-red-200'
              : 'border-gray-300 bg-white focus:border-gray-500 focus:ring-gray-200'
            }
            focus:outline-none focus:ring-2`}
          placeholder={`Search and select ${label.toLowerCase()}...`}
        />
        {value && (
          <a
            href={editUrl}
            target="_blank"
            rel="noopener noreferrer"
            className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-500 hover:text-gray-700 transition-colors"
          >
            <ExternalLink className="w-4 h-4" />
          </a>
        )}
      </div>

      {showDropdown && (
        <div className="absolute z-10 w-full mt-1 bg-white border-2 border-gray-300 rounded-lg shadow-lg max-h-60 overflow-y-auto">
          {items.length > 0 ? (
            items.map((item: any) => (
              <div
                key={item.id}
                onClick={() => {
                  onSelect(item.id);
                  setSearchValue(`${item.id} | ${item.name} | ${item.phone}`);
                  setShowDropdown(false);
                }}
                className="p-3 hover:bg-gray-100 cursor-pointer border-b border-gray-200 last:border-b-0 transition-colors"
              >
                {renderItem(item)}
              </div>
            ))
          ) : (
            <div className="p-3 text-gray-500 text-center">
              {patientSearch || doctorSearch || volunteerSearch || nurseSearch ? 'No results found' : 'No data available'}
            </div>
          )}
        </div>
      )}

      {error && <p className="text-red-500 text-sm mt-1">{error}</p>}
    </div>
  );

  // Close dropdowns when clicking outside
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      const target = event.target as HTMLElement;
      if (!target.closest('.searchable-select')) {
        setShowPatientDropdown(false);
        setShowDoctorDropdown(false);
        setShowVolunteerDropdown(false);
        setShowNurseDropdown(false);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, []);

  return (
    <div className="min-h-screen w-full bg-gray-50 px-4 py-4">
      <div className="max-w-6xl mx-auto">
        {/* Header */}
        <div className="bg-gray-800 text-white p-4 text-center rounded-t-lg">
          <div className="flex justify-center mb-2">
            <Users className="w-10 h-10 text-gray-300" />
          </div>
          <h1 className="text-2xl font-semibold mb-1">
            Healthcare Assignment Form
          </h1>
          <p className="text-sm text-gray-300">
            Assign patients to healthcare professionals
          </p>
        </div>

        {/* Form */}
        <div className="bg-white p-4 rounded-b-lg shadow-lg">
          <div className="space-y-4">

            {/* Assignment Details Section */}
            <div className="bg-gray-50 p-4 rounded-lg border border-gray-200">
              <div className="flex items-center mb-4">
                <FileText className="w-5 h-5 text-gray-600 mr-2" />
                <h3 className="text-lg font-semibold text-gray-800">Assignment Details</h3>
              </div>

              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <div>
                  <label className="block text-sm font-semibold text-gray-700 mb-2 uppercase tracking-wide">
                    Assignment ID <span className="text-red-500">*</span>
                  </label>
                  <input
                    type="text"
                    name="assignmentId"
                    value={formData.assignmentId}
                    className="w-full p-2 border-2 border-gray-300 rounded-lg text-gray-800 font-medium bg-gray-100 cursor-not-allowed text-sm"
                    placeholder="Auto-generated ID"
                    readOnly
                  />
                </div>

                <div>
                  <label className="block text-sm font-semibold text-gray-700 mb-2 uppercase tracking-wide">
                    Date <span className="text-red-500">*</span>
                  </label>
                  <div className="relative">
                    <Calendar className="absolute left-3 top-1/2 transform -translate-y-1/2 w-4 h-4 text-gray-400" />
                    <input
                      type="date"
                      name="date"
                      value={formData.date}
                      onChange={handleInputChange}
                      className="w-full p-2 pl-10 border-2 border-gray-300 rounded-lg text-gray-800 font-medium focus:border-gray-500 focus:ring-2 focus:ring-gray-200 focus:outline-none text-sm"
                    />
                  </div>
                </div>

                <div>
                  <label className="block text-sm font-semibold text-gray-700 mb-2 uppercase tracking-wide">
                    Time <span className="text-red-500">*</span>
                  </label>
                  <div className="relative">
                    <Clock className="absolute left-3 top-1/2 transform -translate-y-1/2 w-4 h-4 text-gray-400" />
                    <input
                      type="time"
                      name="time"
                      value={formData.time}
                      onChange={handleInputChange}
                      className="w-full p-2 pl-10 border-2 border-gray-300 rounded-lg text-gray-800 font-medium focus:border-gray-500 focus:ring-2 focus:ring-gray-200 focus:outline-none text-sm"
                    />
                  </div>
                </div>
              </div>
            </div>

            {/* Patient and Doctor Selection - Horizontal Layout */}
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-4">
              {/* Patient Selection */}
              <div className="bg-gray-50 p-4 rounded-lg border border-gray-200 searchable-select">
                <div className="flex items-center mb-4">
                  <User className="w-5 h-5 text-gray-600 mr-2" />
                  <h3 className="text-lg font-semibold text-gray-800">Patient Assignment</h3>
                </div>

                <SearchableSelect
                  label="Select Patient"
                  value={formData.patientId}
                  searchValue={patientSearch}
                  setSearchValue={setPatientSearch}
                  showDropdown={showPatientDropdown}
                  setShowDropdown={setShowPatientDropdown}
                  items={filterPatients()}
                  onSelect={(id: string) => setFormData(prev => ({ ...prev, patientId: id }))}
                  renderItem={(patient: Patient) => (
                    <div>
                      <div className="font-semibold text-gray-800">{patient.id} | {patient.name}</div>
                      <div className="text-sm text-gray-600">Age: {patient.age} | Ph: {patient.phone}</div>
                    </div>
                  )}
                  editUrl={formData.patientId ? `/viewpatients/patient_reg?edit=true&id=${formData.patientId}` : undefined}
                  error={errors.patientId}
                />
              </div>

              {/* Doctor Selection */}
              <div className="bg-gray-50 p-4 rounded-lg border border-gray-200 searchable-select">
                <div className="flex items-center mb-4">
                  <UserCheck className="w-5 h-5 text-gray-600 mr-2" />
                  <h3 className="text-lg font-semibold text-gray-800">Doctor Assignment</h3>
                </div>

                <SearchableSelect
                  label="Select Doctor"
                  value={formData.doctorId}
                  searchValue={doctorSearch}
                  setSearchValue={setDoctorSearch}
                  showDropdown={showDoctorDropdown}
                  setShowDropdown={setShowDoctorDropdown}
                  items={filterDoctors()}
                  onSelect={(id: string) => setFormData(prev => ({ ...prev, doctorId: id }))}
                  renderItem={(doctor: Doctor) => (
                    <div>
                      <div className="font-semibold text-gray-800">{doctor.id} | {doctor.name}</div>
                      <div className="text-sm text-gray-600">Ph: {doctor.phone}</div>
                    </div>
                  )}
                  editUrl={formData.doctorId ? `/viewdoctor/doctor?edit=true&id=${formData.doctorId}` : undefined}
                  error={errors.doctorId}
                />
              </div>
            </div>


            {/* Volunteer and Nurse Selection - Horizontal Layout */}
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-4">
              {/* Volunteer Selection */}
              <div className="bg-gray-50 p-4 rounded-lg border border-gray-200 searchable-select">
                <div className="flex items-center mb-4">
                  <Heart className="w-5 h-5 text-gray-600 mr-2" />
                  <h3 className="text-lg font-semibold text-gray-800">Volunteer Assignment</h3>
                </div>

                <SearchableSelect
                  label="Select Volunteer"
                  value={formData.volunteerId}
                  searchValue={volunteerSearch}
                  setSearchValue={setVolunteerSearch}
                  showDropdown={showVolunteerDropdown}
                  setShowDropdown={setShowVolunteerDropdown}
                  items={filterVolunteers()}
                  onSelect={(id: string) => setFormData(prev => ({ ...prev, volunteerId: id }))}
                  renderItem={(volunteer: Volunteer) => (
                    <div>
                      <div className="font-semibold text-gray-800">{volunteer.id} | {volunteer.name}</div>
                      <div className="text-sm text-gray-600">Ph: {volunteer.phone}</div>
                    </div>
                  )}
                  editUrl={formData.volunteerId ? `/viewvolunteers/volunteer?edit=true&id=${formData.volunteerId}` : undefined}
                  error={errors.volunteerId}
                />
              </div>

              {/* Nurse Selection */}
              <div className="bg-gray-50 p-4 rounded-lg border border-gray-200 searchable-select">
                <div className="flex items-center mb-4">
                  <Users className="w-5 h-5 text-gray-600 mr-2" />
                  <h3 className="text-lg font-semibold text-gray-800">Nurse Assignment</h3>
                </div>

                <SearchableSelect
                  label="Select Nurse"
                  value={formData.nurseId}
                  searchValue={nurseSearch}
                  setSearchValue={setNurseSearch}
                  showDropdown={showNurseDropdown}
                  setShowDropdown={setShowNurseDropdown}
                  items={filterNurses()}
                  onSelect={(id: string) => setFormData(prev => ({ ...prev, nurseId: id }))}
                  renderItem={(nurse: Nurse) => (
                    <div>
                      <div className="font-semibold text-gray-800">{nurse.id} | {nurse.name}</div>
                      <div className="text-sm text-gray-600">Ph: {nurse.phone}</div>
                    </div>
                  )}
                  editUrl={formData.nurseId ? `/viewnurses/nurse?edit=true&id=${formData.nurseId}` : undefined}
                  error={errors.nurseId}
                />
              </div>
            </div>


            {/* Description */}
            <div className="bg-gray-50 p-4 rounded-lg border border-gray-200">
              <div className="flex items-center mb-4">
                <FileText className="w-5 h-5 text-gray-600 mr-2" />
                <h3 className="text-lg font-semibold text-gray-800">Assignment Description</h3>
              </div>

              <div>
                <label className="block text-sm font-semibold text-gray-700 mb-2 uppercase tracking-wide">
                  Description <span className="text-red-500">*</span>
                </label>
                <textarea
                  name="description"
                  value={formData.description}
                  onChange={handleInputChange}
                  rows={3}
                  className={`w-full p-2 border-2 rounded-lg text-gray-800 font-medium transition-all duration-300 text-sm resize-none
                    ${errors.description
                      ? 'border-red-400 bg-red-50 focus:border-red-500 focus:ring-red-200'
                      : 'border-gray-300 bg-white focus:border-gray-500 focus:ring-gray-200'
                    }
                    focus:outline-none focus:ring-2`}
                  placeholder="Provide detailed information about this assignment, including care instructions, special requirements, or any relevant notes..."
                />
                {errors.description && <p className="text-red-500 text-sm mt-1">{errors.description}</p>}
              </div>
            </div>

            {/* Submit Button */}
            <div className="flex justify-center pt-6">
              <button
                onClick={handleSubmit}
                className="bg-gray-800 hover:bg-gray-900 text-white font-semibold py-3 px-8 rounded-lg text-base
                         transform transition-all duration-300 hover:scale-105 hover:shadow-lg
                         focus:outline-none focus:ring-4 focus:ring-gray-300"
              >
                Create Assignment
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AssignmentForm;